'use client'

import { fetchConfig } from "@/api/fetchConfig"
import { api } from "@/pages/api/api"
import { APICOMMAND } from "@/shared/types/command.types"
import { Task } from "@/shared/types/task.types"
import { User } from "@/shared/types/user.types"
import { useEffect, useState } from "react"

interface Props {
    data: Task
}

export function TabComletedTask({data}: Props) {
    
    const [author, setAuthor] = useState<User | null>(null)

    useEffect(() => {
        async function fetchUsers () {
            const config = await fetchConfig()
            const response = await api(APICOMMAND.GETUSERS, {}, config)
            const dataUsers: User[] = await response?.json()
            const filteredAuthor = dataUsers.filter(item => item.id === data.authorid)
            setAuthor(filteredAuthor[0])
        }

        fetchUsers()
    }, [])

    return (
        <>
            <td className="border-r-1 border-gray-300 p-2">
                {data.statusid.namestatus}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.nametask}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.info}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.datecreate}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.datechange}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.dateestimatedcompletion}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.dateend}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {`${author?.firstname} ${author?.lastname}`}
            </td>
        </>
    )
}