import LeftMenuBtn from "../../components/ui/LeftMenuBtn/LeftMenuBtn"

const btnIconArr = [
    {
        imgSrc: './task.svg',
        tag: 'Отслеживание заданий'
    },
    {
        imgSrc: './plus.svg',
        tag: 'Добавить задание'
    },
    {
        imgSrc: './qu.svg',
        tag: 'О программе'
    },
    {
        imgSrc: './logout.svg',
        tag: 'Выход'
    }
]

function LeftMenu() {
    return (
            <div className="flex flex-col gap-[64px] pt-4 items-start justify-start border-r-1 border-r-gray-500 w-60 h-screen bg-[#243657]">
                <picture className="block pl-3">
                    <img className="w-10 h-auto" src="./white-logo.png"/>
                </picture>
                <ul className="flex flex-col items-start gap-[6px] w-full">
                    {btnIconArr.map((item) => (
                        <li key={item.tag} className="w-full flex items-center justify-start gap-[12px] pt-2 pb-2 pl-1 border-l-3 border-transparent bg-transparent cursor-pointer hover:border-l-3 hover:border-[#008888] hover:bg-[linear-gradient(to_right,_rgba(0,136,136,0.2),_rgba(0,136,136,0))] transition-all duration-300">
                            <LeftMenuBtn iconTag={item.tag}>
                                <img className="block w-8 h-8" src={item.imgSrc}/>
                            </LeftMenuBtn>
                        </li>
                    ))}
                </ul>
            </div>
    )
}

export default LeftMenu