'use client'

export function Select() {
    return (
        <label className="flex flex-col gap-[4px]">
            <span className="font-light text-sm text-gray-400">Статус выполнения</span>
            <select className="bg-white w-60 h-10 rounded text-sm text-black-300 font-normal">
                <option value="Задача назначена">Задача назначена</option>
                <option value="Задача принята исполнителем">Задача принята исполнителем</option>
                <option value="Задача в работе">Задача в работе</option>
                <option value="Задача завершена">Задача завершена</option>
                <option value="Задача отменена">Задача отменена</option>
                <option value="Задача изменена">Задача изменена</option>
            </select>
        </label>
    )
}
